                e.preventDefault()
        },
        valida_telefono(e){
            if (this.telefono.length >= 10 || isNaN(parseInt(e.key)))
                e.preventDefault()
        },
        valida_correo(e){
            if (this.correo.length >= 30)
                e.preventDefault()
        },
        valida_domicilio(e){
            if(this.domicilio.length >= 80)
